.navigation__button {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
  right: 23px;
  top: 65px;
  margin-left: 14px;
  border: none;
  text-decoration: none;
  width: 20px;
  height: 22px;
  background-color: transparent;
  list-style: none;
  cursor: pointer;
  /* outline: 1px solid red; */
}

.navigation__button:hover {
  transform: rotate(25deg);
  transition: transform 0.2s;
}

.navigation__button_inactive {
  display: none;
}

.navigation__span {
  width: 17px;
  height: 4px;
  background-color: gray;
  padding: 0;
}

.navigation__span:nth-child(2) {
  margin-top: 4px;
}

.navigation__span:nth-child(3) {
  margin-top: 4px;
}

.navigation__span_menu-active:nth-child(1) {
  display: none;
}

.navigation__span_menu-active:nth-child(2) {
  width: 23px;
  transform: rotate(-48deg);
  transition: transform 0.35s;
}

.navigation__span_menu-active:nth-child(3) {
  width: 23px;
  margin-top: -4px;
  transform: rotate(42deg);
  transition: transform 0.35s;
}

.navigation__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 0;
  right: 0;
  width: 288px;
  height: 812px;
  padding: 15px;
  background: #f4f4f4cf;
  transform: translateX(100%);
  transition: transform 0.3s;
  visibility: hidden;
  /* outline: 1px solid red; */
}

.navigation__menu_active {
  transform: translateX(0);
  transition: transform 0.75s;
  visibility: visible;
}


.navigation__nav-container {
  margin: 35px 0 0 0;
  padding: 0;
  height: 395px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.navigation__menu-link {
  /* outline: 1px solid red; */
  font-size: 34px;
  line-height: 42px;
  color: #828282;
  cursor: pointer;
}

.navigation__menu-link:hover {
  color: #090909;
  transition: color 0.5s;
}

.navigation__menu-lang {
  width: 160px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  transform: rotate(90deg);
  margin-left: 187px;
  margin-bottom: 90px;
}

.navigation__menu-button-lang {
  margin: 0;
  font-size: 32px;
  line-height: 42px;
  color: #828282;
  border: none;
  text-decoration: none;
  cursor: pointer;
  background-color: transparent;
}

.navigation__menu-button-lang:hover {
  color: #090909;
  transition: color 0.5s;
}

.navigation__menu-button-lang_active {
  color: #000000;
}

